
#map {
  height: 100%;
}
.fab {
  position: absolute;
  bottom: 16px;
  right: 16px;
}
@media only screen and (min-width: 600px) {
.fab {
    position: absolute;
    bottom: 24px;
    right: 24px;
}
}
.post {
  position: absolute;
  bottom: 24px;
  left: 0;
  right: 0;
  margin: auto
}



